body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* reset.css */
ul{
  list-style: none;
}
body,
div,p,
span,
a,img,
ul,ol,
h1,h2,h3,h4,h5,h6{
  padding: 0;
  margin: 0;
}
:root{
  --themeColor:#44883e;
  --themeHoverColor:#080;
  --hoverColor:#ff5c38;
  --highlightColor:#f00;
  --backColor:#2d2e36;
  --foreColor:#fff;
  --oneOfSix:16.666667%;
}
@font-face{src:url(data:application/font-sfnt;charset=utf-8;base64,) format("truetype");font-display:swap;font-family:numFont}

html{
  font-size: 20px;
}
body{
  font-family: numFont, PingFangSC-Regular, Tahoma, "Microsoft Yahei", sans-serif;
}
.clearfix:after{
  content: "";
  clear: both;
  height: 0;
  visibility: hidden;
}

.raw-title{
  font-size: 1.5rem;
}
.page{
  min-width: 1000px;
}

/* ant-tag */
.ant-tag.tag-orange{
  background-color: #ff5c38;
  background-color: var(--hoverColor);
  border-color: #ff5c38;
  border-color: var(--hoverColor);
  color:#fff;
}

:root {
  --rankFirst: #ff183e;
  --rankSecond: #ff5c38;
  --rankThird: #ffb821;
  --rankOther: #666;
}

.header {
  position: relative;
  margin: 0 0 50px 0;
}

.header .header-bar {
  position: relative;
  padding: 10px 0;
  background-color: rgba(255, 255, 255, .24);
  z-index: 2;
}

.header .head-bar--fixed {
  box-shadow: 0 28px 50px rgba(25, 24, 40, .35);
  background-color: rgba(255, 255, 255, .95);
}

.bar-container {
  margin: 0 10%;
}

.header .header-bar .logo {
  display: inline-block;
  width: 125px;
  height: 25px;
  background-image: url(/static/media/douban-logo.22879ba5.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.header .header-bar .slot-title {
  display: inline-block;
  margin: 0 0 0 15px;
}

.header .header-bar .search {
  float: right;
  position: relative;
}

.header .header-bar .search-box {
  float: right;
  height: 40px;
  border-radius: 20px;
  background-color: rgba(255, 255, 255, .2);
  box-shadow: 0 4px 18px rgba(17, 18, 38, .07);
  border: 1px solid transparent;
  overflow: hidden;
}

.header .head-bar--fixed .search-box {
  background-color: transparent;
  box-shadow: none;
  border-color: var(--themeColor);
}

.header .header-bar .search-box .search-input, .header .header-bar .search-box .search-btn {
  float: right;
  height: 100%;
}

.header .header-bar .search-box .search-input {
  padding: 0 10px;
  margin: 0 0 0 20px;
  background-color: transparent;
  border: none;
  outline: none;
}

.header .header-bar .search-box .search-btn {
  width: 100px;
  text-align: center;
  line-height: 40px;
  background-color: var(--themeColor);
  color: var(--foreColor);
  cursor: pointer;
}

.header .header-bar .search-box .search-btn:hover {
  background-color: var(--themeHoverColor);
}

.header .header-bar .search-list {
  position: absolute;
  top: 100%;
  left: 15px;
  right: 15px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 28px 50px rgba(25, 24, 40, .35)
}

.header .header-bar .search-list .list-history .panel-title, .header .header-bar .search-list .list-hot .panel-title {
  padding: 5px 10px;
  color: #999;
}

.header .header-bar .search-list .list-item {
  position: relative;
  margin: 5px 5px;
  padding: 5px;
  border-radius: 3px;
}

.header .header-bar .search-list .list-item:hover {
  background-color: #eee;
}

.list-history .list-item {
  display: inline-block;
}

.header .header-bar .search-list .list-item .index {
  display: inline-block;
  background-color: #666;
  background-color: var(--rankOther);
  color: #fff;
  width: 20px;
  line-height: 20px;
  margin: 0 10px 0 0;
  text-align: center;
  border-radius: 3px;
}

.block-weekly .weekly-box .card-container:nth-of-type(1) .dot::before, .block-weekly .weekly-box .card-container:nth-of-type(1) .dot::after, .header .header-bar .search-list .list-item:nth-of-type(1) .index {
  background-color: #ff183e;
  background-color: var(--rankFirst);
}

.block-weekly .weekly-box .card-container:nth-of-type(2) .dot::before, .block-weekly .weekly-box .card-container:nth-of-type(2) .dot::after, .header .header-bar .search-list .list-item:nth-of-type(2) .index {
  background-color: #ff5c38;
  background-color: var(--rankSecond);
}

.block-weekly .weekly-box .card-container:nth-of-type(3) .dot::before, .block-weekly .weekly-box .card-container:nth-of-type(3) .dot::after, .header .header-bar .search-list .list-item:nth-of-type(3) .index {
  background-color: #ffb821;
  background-color: var(--rankThird);
}

.header .header-bar .search-list .list-item .title {
  color: #000;
}

.header .header-banner {
  background-image: url(/static/media/banner-blur-bg.7916debb.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.header .header-banner .banner-item {
  height: 500px;
}

.header .header-banner .banner-item img {
  display: inline;
  display: initial;
  width: 100%;
  height: 100%;
}

.page-home {
  width: 80%;
  margin: 0 auto;
}

.page-home .block {
  margin: 0 0 50px 0;
}

.block-weekly .weekly-box {
  margin: 50px 0 0 0;
  border-top: 1px solid #f0f0f0;
  text-align: center;
}

.block-top250 .cards-box.cards-box--top250 {
  position: relative;
  padding: 0 0 0 calc(2* var(--oneOfSix));
  margin: 0 0 30px 0;
}

.cards-box .card-container {
  width: var(--oneOfSix);
  display: inline-block;
}

.block-top250 .cards-box .card-container {
  width: 25%;
}

.block-top250 .cards-box .card-container.card-big {
  position: absolute;
  top: 0;
  left: 0;
  width: calc(2* var(--oneOfSix));
  bottom: 20px;
}

.block-top250 .cards-box .card-container.card-big .movie-card {
  height: 100%;
}

.block-top250 .cards-box .card-container.card-big .movie-card .card-img, .block-top250 .cards-box .card-container.card-big .loading-img-box {
  height: 600px;
}

.block-top250 .cards-box .card-container.card-big .movie-card .img-tag {
  top: 10px;
  left: 10px;
  font-size: 2rem;
  line-height: 2rem;
  border-radius: 8px;
}

.block-top250 .cards-box .card-container.card-big .movie-card .ant-card-meta-title {
  font-size: 1.2rem;
}

.block-top250 .cards-box .card-container.card-big .movie-card .ant-card-meta-description {
  font-size: .8rem;
}

.block-newmovie .card-container {
  width: 25%;
}

.block-weekly .weekly-box .card-container {
  position: relative;
  padding: 15px 0 10px 0;
}

.block-weekly .weekly-box .card-container .rate {
  font-size: .9rem;
  font-weight: 700px;
  color: var(--backColor);
}

.block-weekly .weekly-box .card-container .title {
  color: #999;
}

.block-weekly .weekly-box .card-container .dot {
  position: absolute;
  top: -6px;
  /* (width-1)/2+1 */
  left: 50%;
  width: 11px;
  height: 11px;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}

.block-weekly .weekly-box .card-container .dot::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #666;
  background-color: var(--rankOther);
}

.block-weekly .weekly-box .card-container .dot::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 58%;
  height: 58%;
  -webkit-transform: translate(-50%, 0) rotate(45deg);
          transform: translate(-50%, 0) rotate(45deg);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  background-color: #666;
  background-color: var(--rankOther);
}

.block-weekly .line-raw {
  position: relative;
}

.block-weekly .line-raw .spotbox {
  position: absolute;
  top: 0;
  right: 0;
  display: -webkit-flex;
  display: flex;
}

.block-weekly .line-raw .spotbox .spot {
  position: relative;
  margin: 0 10px;
}

.block-weekly .line-raw .spotbox .spot:nth-of-type(1) {
  top: 40%;
  width: 20px;
  height: 50px;
  background-image: -webkit-linear-gradient(#ffb821, #fff);
  background-image: linear-gradient(#ffb821, #fff);
  background-image: -webkit-linear-gradient(var(--rankThird), #fff);
  background-image: linear-gradient(var(--rankThird), #fff);
  border-radius: 10px 10px 0 0;
}

.block-weekly .line-raw .spotbox .spot:nth-of-type(2) {
  top: -30px;
  width: 26px;
  height: 50px;
  background-image: -webkit-linear-gradient(#ff183e, #fff);
  background-image: linear-gradient(#ff183e, #fff);
  background-image: -webkit-linear-gradient(var(--rankFirst), #fff);
  background-image: linear-gradient(var(--rankFirst), #fff);
  border-radius: 13px 13px 0 0;
}

.block-weekly .line-raw .spotbox .spot:nth-of-type(3) {
  width: 14px;
  height: 50px;
  background-image: -webkit-linear-gradient(#ff5c38, #fff);
  background-image: linear-gradient(#ff5c38, #fff);
  background-image: -webkit-linear-gradient(var(--rankSecond), #fff);
  background-image: linear-gradient(var(--rankSecond), #fff);
  border-radius: 7px 7px 0 0;
}

.movie-card {
  margin: 10px 8px 10px 0;
}

.block-hotshow .movie-card:nth-of-type(6n) {
  margin-right: 0;
}

.movie-card .ant-card-body {
  padding: 5px 10px;
}

.movie-card .ant-card-body .ant-card-meta-detail>div:not(:last-child) {
  margin: 0;
}

.loading-img-box, .movie-card img {
  width: 100%;
  height: 300px;
}

.loading-img-box {
  position: relative;
  background-color: #f5f6f7;
  cursor: not-allowed;
}

.loading-img-box img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: auto;
  height: auto;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.block-newmovie {
  position: relative;
}

.block-newmovie .cards-box {
  width: calc(var(--oneOfSix) * 4);
}

.rate-box {
  position: absolute;
  top: 0;
  right: 0;
  width: 32%;
}

.rate-box .goodbox {
  background-color: #f8f8f8;
}

.rate-box .goodbox .goodbox-rate {
  position: relative;
  margin: 10px 20px;
  padding: 10px 50px;
  border-top: 1px solid #eee;
}

.rate-box .goodbox .goodbox-rate .title {
  font-size: .9rem;
  font-weight: 800;
}

.rate-box .goodbox .goodbox-rate .title:hover {
  color: var(--themeHoverColor);
}

.rate-box .goodbox .goodbox-rate .rank {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 2rem;
  color: #666;
  color: var(--rankOther);
}

.rate-box .goodbox .goodbox-rate:nth-of-type(-n+3) .rank {
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
          background-clip: text;
  background-image: -webkit-linear-gradient(top, #ffb821 0, #ff5c38 45%, #ff1459);
  background-image: linear-gradient(180deg, #ffb821 0, #ff5c38 45%, #ff1459);
}

.rate-box .goodbox .goodbox-rate .box {
  position: absolute;
  top: 10px;
  right: 0;
  color: #999;
}

.rate-box .goodbox .goodbox-rate .summary {
  color: #999;
}

.rate-box .goodbox .goodbox-rate .box-new {
  color: var(--highlightColor);
}

.footer {
  border-top: 2px solid var(--themeColor);
  padding: 40px 0;
  font-size: 0.6rem;
  text-align: center;
  color: #999;
}

.footer-block {
  line-height: 1.2rem;
}

.footer-block a {
  color: inherit;
}

.footer-block a:hover {
  color: var(--themeHoverColor);
}

.poster-box {
  height: 320px;
  background-color: var(--backColor);
}

.profile {
  position: relative;
  padding: 100px 10%;
}

.profile .block {
  margin: 0 0 50px 0;
}

.profile-img {
  width: 200px;
  box-shadow: 2px 2px 3px 0px #121111;
}

.profile-img img {
  width: 100%;
  height: auto;
}

.profile-info .tags {
  margin: 10px 0;
}

.profile-info .tags .tag-text {
  background-color: var(--backColor);
  color: var(--foreColor);
}

.video_summary {
  margin: 20px 0;
}

.profile-rate {
  position: absolute;
  top: 100px;
  right: 10%;
}

.profile-rate .rate {
  color: var(--hoverColor);
}

.profile-rate .rate .units {
  font-size: 72px;
}

.profile-rate .rate .decimal {
  font-size: 54px;
}

.profile-rate .box {
  color: #fff;
  font-size: 1rem;
}

.profile-rate .title {
  position: absolute;
  top: 0;
  right: 120px;
  width: 6em;
  font-size: 5rem;
  color: #fff;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: center;
}

.photos-box {
  overflow-x: auto;
  overflow-y: hidden;
  height: 200px;
}

.box-gallery {
  width: 2000px;
  height: 200px;
}

.gallery-img {
  position: relative;
  float: left;
  height: 100%;
  border-right: 5px solid #fff;
}

.gallery-img:last-of-type {
  border-right: none;
}

.gallery-img img {
  width: auto;
  height: 100%;
}

.gallery-img .img-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  font-size: 2rem;
  color: #fff;
}
.movie-card .img-tag, .gallery-img .img-tag {
  position: absolute;
  top: 5px;
  left: 5px;
}

.container-video {
  padding: 0 10%;
}

.video-box {
  position: relative;
  height: 400px;
  overflow: hidden;
  background-color: #27272f;
}

.video-box video {
  width: 700px;
  height: 100%;
}

.video-box video:focus {
  outline: none;
}

.box-list {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: calc(100% - 750px);
  padding: 0 0 0 15px;
  overflow-x: hidden;
  overflow-y: auto;
}

.box-list .list .list-item {
  cursor: pointer;
}

.box-list .list .list-item img {
  width: 200px;
  height: auto;
}

.box-list .list .list-item.active .ant-list-item-meta-title,
.box-list .list .list-item:hover .ant-list-item-meta-title{
  color: var(--themeHoverColor);
}

.drawer-in {
  top: 100%;
  -webkit-transition: top 1s ease;
  transition: top 1s ease;
}

.drawer-fixed-bottom {
  position: fixed;
  top: 460px;
  /* The width of top drawer */
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 1001;
  background-color: #FFF;
  padding: 10px 10%;
  overflow: auto;
}

.profile-img-loading {
  width: 200px;
  height: 280px;
  background-color: #f5f6f7;
  background-image: url(/static/media/loading.130d74ed.svg);
  background-repeat: no-repeat;
  background-position: center;
}

/* custom ant style */
.custom-ant-rate {
  font-size: .7rem;
  margin: 0 5px;
}

.cutsom-ant-comment {
  border-bottom: 1px solid #f0f0f0;
}

/* reset ant style */
.ant-drawer .ant-drawer-content {
  background-color: var(--backColor);
}

.ant-drawer .ant-list-vertical .ant-list-item-meta-title {
  color: #fff;
}

.ant-drawer .ant-drawer-close {
  color: #fff;
}

.page-search{
  position: relative;
  width: 80%;
  margin: 30px auto;
}

.page-search .search-result-list{
  width: 70%;
}

.page-search .search-result-list .item-img{
  width:160px;
  height:220px;
  border-radius: 5px;
}

.page-search .ant-list-split .ant-list-item{
  border:none;
}

.page-search .rate-box{
  width:30%;
}

.page-search .rate-box .ant-affix{
  z-index:9;
}

.page-search .rate-box .goodbox .goodbox-rate{
  margin:5px 10px;
  padding: 5px 30px;
}

.page-search .rate-box .goodbox .goodbox-rate .title{
  font-size: .8rem;
}

.page-search .rate-box .goodbox .goodbox-rate .rank{
  font-size: 1rem;
}

.page-404 {
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: -webkit-linear-gradient(45deg, #080 50%, #fff 50%);
  background-image: linear-gradient(45deg, #080 50%, #fff 50%);
}

.page-404 .ant-empty {
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  color: #fff;
}

.header-404 .page-404-title {
  font-size: 2rem;
  text-align: center;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
          background-clip: text;
  background-image: -webkit-linear-gradient(top, #ffb821 0, #ff5c38 45%, #ff1459);
  background-image: linear-gradient(180deg, #ffb821 0, #ff5c38 45%, #ff1459);
}

.header-404 {
  width: 100%;
  padding: 15px 0;
  position: fixed;
  top: 30%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.header-404 .header-bar {
  width: 60%;
  margin: 0 auto;
  background-color: transparent;
}

.header-404 .bar-container .bar-top {
  display: inline-block;
  width: 100%;
  text-align: center;
}

.header-404 .header-bar .logo {
  height: 34px;
  width: 160px;
}

.header-404 .header-bar .search-list, .header-404 .header-bar .search-box {
  border-radius: 0;
}

.header-404 .header-bar .search-box {
  background-color: rgba(255, 255, 255, .85);
  box-shadow: none;
  border-color: var(--themeColor);
}

.header-404 .header-bar .search-list {
  left: 0;
  right: 0;
}

.header-404 .header-bar .search-box .search-input {
  width: 500px;
}

