/* reset.css */
ul{
  list-style: none;
}
body,
div,p,
span,
a,img,
ul,ol,
h1,h2,h3,h4,h5,h6{
  padding: 0;
  margin: 0;
}
:root{
  --themeColor:#44883e;
  --themeHoverColor:#080;
  --hoverColor:#ff5c38;
  --highlightColor:#f00;
  --backColor:#2d2e36;
  --foreColor:#fff;
  --oneOfSix:16.666667%;
}
@font-face{src:url(data:application/font-sfnt;charset=utf-8;base64,) format("truetype");font-display:swap;font-family:numFont}

html{
  font-size: 20px;
}
body{
  font-family: numFont, PingFangSC-Regular, Tahoma, "Microsoft Yahei", sans-serif;
}
.clearfix:after{
  content: "";
  clear: both;
  height: 0;
  visibility: hidden;
}

.raw-title{
  font-size: 1.5rem;
}
.page{
  min-width: 1000px;
}

/* ant-tag */
.ant-tag.tag-orange{
  background-color: var(--hoverColor);
  border-color: var(--hoverColor);
  color:#fff;
}
